.org-event {
	position: relative;
	display: flex;
	flex-direction: column;
	border-bottom: 1px solid var(--divider-color);
	margin-bottom: 12px;
	padding: 6px 0;
	
	&__headline {
		display: block;
		align-items: center;
		justify-content: space-between;
	}
	&__title {
		font-weight: 600;
		color: var(--text-color);
	}
	
	&__date-time {
		font-size: 14px;
		font-weight: 800;
		margin-right: 6px;
		color: var(--accent-color);
	}
	&__description {
		font-size: 12px;
		color: var(--text-color);
		margin-top: 6px;
	}
	&__buttons {
		position: absolute;
		display: flex;
		flex-direction: row;
		top: 6px;
		right: 0;
	}
	&__button {
		width: 18px;
		height: 18px;
		cursor: pointer;
		transition: all .15s ease-in-out;
		
		&:hover {
			color: var(--accent-color);
		}
		&:first-child {
			margin-right: 6px;
		}
	}
}
